.UserDialog-Wrapper{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.UserDialog{
  border-radius: 4px;
  min-width: 20em;
  background: white;
  border: 1px solid #aaa;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
}
.UserDialog > nav{
  padding: 1em;
  margin-left: -2em;
}
.UserDialog > nav label{
  margin-left: 2em;
  cursor: pointer;
}
.UserDialog > .panes{
  padding: 0 1em 1em;
}
.UserDialog form > .row{
  display: flex;
  padding: 0.3em 0;
}
.UserDialog form > .row > label:first-child{
  width: 4em;
}
.UserDialog form > .row.actions{
  padding-left: 4em;
}